import { NextResponse } from "next/server"

export function GET(){
    try {
        return NextResponse.json('Obteniendo nueva tarea')
    } catch (error) {
        console.log(error.message)
    }
}
export function POST(){
    try {
        return NextResponse.json('Creando nueva tarea')
    } catch (error) {
        console.log(error.message)
    }
}